/******************** Principales *********************/

html {
    vertical-align: middle;
    height: 100%;
}

body {
    background-color: rgb(133, 130, 130) !important;
    background-position: center;
    background-size: cover;
    text-align: center;
}

footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: rgb(102, 100, 100);
    color: white;
    text-align: center;
    font-style: italic;
}

.hide {
    display: none;
}

#main {
    display: inline-block;
    margin: auto;
    width: 80%;
    height: 80vh;
    text-align: center;
}

textarea {
    background-color: rgb(59, 59, 54)!important;
    color: white!important;
    font-weight: lighter!important;
    font-family: Inconsolata!important;
}

#input {
    width: 50%;
    display: block;
    float: left;
}

#output {
    width: 50%;
    display: block;
    float: right;
}

#submitCode,
#runCode {
    background: url(http://i.imgur.com/2cOaJ.png);
    background-size: 29px;
    background-attachment: local;
    background-repeat: no-repeat;
    padding-left: 35px;
    padding-top: 10px;
    font-size: 16px;
    line-height: 18px;
}

#runnable {
    width: 50%;
}

.options {
    margin-top: 5px!important;
    margin-bottom: 5px;
    text-align: left;
}

#input #submitCode {
    height: 40vh!important;
}

#input #runCode {
    height: 20vh!important;
}

#output textarea {
    height: 65vh!important;
}


/******************** Especificas *********************/

@font-face {
    font-family: 'Inconsolata';
    src: url('/fonts/Inconsolata-VariableFont_wdth,wght.ttf');
}

.Code_Section {
    border-top-width: 10px;
    border-right-width: 1em;
    border-bottom-width: thick;
    border-left-width: thin;
}

.logo>img {
    float: left;
    margin-top: 15px;
    margin-left: 5%;
    height: 40px;
    width: auto;
}

.logo>span {
    font-family: cursive;
    font-size: 2em;
    color: rgb(255, 105, 45)
}

label {
    color: white;
    font-family: Inconsolata;
    font-weight: lighter;
}