header {
    background-color: white;
}

#menu {
    /*padding: 5px;*/
    margin-right: 10%;
    background-color: white;
}

#menu ul {
    margin: 10px;
    padding: 2px 2px 2px 2px;
    list-style: none;
    text-align: right;
    vertical-align: middle;
}

#menu ul li {
    font-size: 13px;
    display: inline-block;
    vertical-align: middle;
}

#menu ul .nav-item a {
    width: 11em;
    color: black;
    font-weight: bold;
    font-family: Open Sans, sans-serif;
    text-transform: uppercase;
    text-decoration: none;
    display: block;
}

#menu .dropdown-menu .dropdown-item {
    width: 100%;
    font-size: 10px;
}

#menu>#navbarNavDropdown>ul>li>a:hover {
    color: rgb(236, 19, 3);
}

#menu>#navbarNavDropdown>ul>li>.dropdown-menu>a:hover {
    color: rgb(236, 19, 3);
}

#navbarNavDropdown {
    background-color: white;
}